<template>
  <div>
      <ul class="nav">
        <li class="nav-item">
            <router-link :to="{name: 'useRoute1',params: {id: 1}}" class="nav-link active" href="#">《1》route-link跳转方式，以对象参数的形式传参</router-link>
        </li>
         <li class="nav-item">
            <router-link :to="`/useRoute1/2`" class="nav-link active" href="#">《2》route-link跳转方式，以模板字符串的形式传参</router-link>
        </li>
        <li class="nav-item">
            <a class="nav-link" @click.prevent="goUseRoute2" href="#">《3》使用JavaScript跳转方式</a>
        </li>
      </ul>
  </div>
</template>

<script lang="ts">
import {defineComponent} from 'vue'
import {useRouter} from 'vue-router'
export default defineComponent({
    components: {

    },
    setup() {
        const router = useRouter()
        const goUseRoute2 = ()=> {
            router.push({
                name: 'useRoute2',
                params: {
                    id: 3
                }
            })
        }
        return {
            goUseRoute2
        }
    }
})
</script>

