
import {defineComponent,onUnmounted} from 'vue'
export default defineComponent({
props: {
    text: {
        type: String,
        default: ""
    },
    background: {
        type: String
    },
    color: {
        type:String,
        default: '#0d6efd'
    }
},
setup() {
    const node = document.createElement('div')
    node.id = "loader"
    document.body.appendChild(node)

    onUnmounted(() => {
        document.body.removeChild(node)
    })
    return
}
})
