
import {computed, defineComponent,PropType} from 'vue'
import {useRouter} from 'vue-router'
import store, {ColumnProps} from '@/store/index'

import Loader from '@/components/Loader/Loader.vue'

export default defineComponent({
    name: 'ColumnList',
    components: {
        Loader
    },
    props: {
        list: {
            type: Array as PropType<ColumnProps[]>,
            required: true
        }
    },
    setup(props) {
        const router = useRouter()
        // props.list[0].id
      
        const goDetailColumn = (id:string) => {
            console.log('进入详情',id);
            
            router.push({
                path:'/columnDetail',
                query: {
                    id: id
                }
                // params: {
                //     id: id
                // }
            })
            
        }
        const columnList = computed(() => {
            return props.list.map(column => {
                if (!column.avatar) {
                    column.avatar = require('@/assets/logo.png')
                }
                return column
            })
        })
        const isLoading = computed(()=>store.state.isLoding)
        return {
            columnList,
            goDetailColumn,
            isLoading
        }
    }
})
