<template>
  <validate-form @form-submit="onFormsubmit">
    <div class="mb-3">
        <label for="exampleInputEmail0" class="form-label">邮箱地址:</label>
        <validate-input ref="inputRef" :rules="emailRules" v-model="emailValue" id="exampleInputEmail0" type="text" placeholder="请输入邮箱地址"></validate-input>
    </div>
  
  <div class="mb-3">
    <label for="exampleInputEmail1" class="form-label">密码</label>
     <validate-input :rules="passwodrRules" id="exampleInputEmail1" v-model="passwordValue" type="password" placeholder="请输入密码"></validate-input>
  </div>

  <!-- <template v-slot:submit>
      <span class="btn btn-danger">submit</span>
  </template> -->

  <!-- 具名插槽的缩写 -->
  <template #submit>
      <span class="btn btn-danger">submit</span>
  </template>
  
</validate-form>
</template>

<script lang='ts'>
/* 使用store 触发store里面的函数 */
/* 登录调用接口 获取token并保存 */
import {useStore} from 'vuex'

import {defineComponent,reactive,ref,computed} from 'vue'
import {useRouter} from 'vue-router'
import validateInput,{RuleProps} from '../validateInput/validateInput.vue'
import ValidateForm from '../ValidateForm/ValidateForm.vue'
import createMessage from '@/hooks/createMessage' // 采用函数调用的形式
export default defineComponent({
    components:{
        validateInput,
        ValidateForm
    },
    setup() {
    //    const  signupDataJson = localStorage.getItem('signupData')
      
    //    let email = signupDataJson
       let email = 'hmg@qq.com'
       let password = '111111'
        const emailValue = ref(email)
        const passwordValue = ref(password)
        const inputRef = ref<any>('')
        const emailRules:RuleProps = [
            {type: 'required',message: '邮箱地址不能为空！！！'},
            {type: 'email',message: '请输入正确的邮箱！！！'}
        ]
        const passwodrRules:RuleProps = [
            {type: 'required',message: '请输入密码！！！'},
        ]
        const emailRef = reactive({
            val: '',
            error: false,
            message: ''
        })

        const router = useRouter()
        const store = useStore()

        const onFormsubmit = (result: boolean)=> {
            if (result) {
                let payload = {
                    email: emailValue.value,
                    password: passwordValue.value
                }
                /* store.dispatch('fetchLogin',payload).then(data => {
                    console.log('登录数据：',data);
                     router.push('/')
                    
                }) */
                store.dispatch('loginAndFeach',payload).then(data => {
                     createMessage('2秒后跳转首页','success')
                    console.log('登录数据：',data);
                    // localStorage.removeItem('signupData')
                    setTimeout(()=> {

                        router.push('/')
                    },2000)
                    
                }).catch(error => {
                    console.log(error);
                    
                })
                // loginAndFeach
            }
            
        }
       
        return {
            emailRef,
            emailRules,
            passwodrRules,
            emailValue,
            passwordValue,
            onFormsubmit,
            inputRef
        }
    }
})
</script>

