
import {defineComponent,ref} from 'vue'

export default defineComponent({
    name: 'DropdownItem',
    props: {
      disabled: {
        type: Boolean,
        required: false
      }
    },
    setup() {
      const isOpen = ref(false)
      const toggleOpen = () => {
        isOpen.value = !isOpen.value
      }
        return {
          isOpen,
          toggleOpen
        }
    }
})
